body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes, .stopwatch__seconds {
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
}

.stopwatch__minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-hands;
}

.stopwatch__minutes--speed-up {
  animation: 600s steps(60, end) infinite rotate-hands;
}

.stopwatch__seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-hands;
}

.stopwatch__seconds--speed-up {
  animation: 10s linear infinite rotate-hands;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

@keyframes rotate-hands {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
